<template>
    <form @submit.prevent="onSubmit" id="loginForm">
      <main id="mainLogin">
        <div class="redDiv title">ESSENTIEL CHAT</div>
        <div class="yellowDiv"></div>
        <div>
            <p class="loginText" style="font-weight: bold">Bonjour</p>
            <input class="loginText" type="text" placeholder="...frappe ton nom ici" v-model="username">
            <p class="error" v-if="error">The username is unvalid</p>
        </div>
        <div></div>
      </main>
      <div></div>
      <button id="loginSubmit" class="blackDiv" type="submit">LOG IN</button>
    </form>
</template>

<script>
  export default {
    props: ['store'],
    data () {
      return {
        error: false,
        username: ''
      }
    },
    methods: {
      onSubmit (e) {
        // Validation
        if (!this.username.match(/^\w{1,15}$/)) {
          this.error = true
        } else {
          this.error = false
          this.$emit('login', this.username)
        }
      }
    },
    created () {
      this.$store.$watch('user', (user) => {
        if (user.id) {
          this.$router.push('/')
        }
      })
    },
    mounted () {
      this.$emit('toggle', false)
    }
  }
</script>

<style lang="stylus">

  #loginForm
    display block
    height 100%

    >div
      width 20%
      height 20%
      box-sizing border-box
      border-right solid 3px black
      float left

  #mainLogin
    width 100%
    height 80%

    >div
      &:first-child
        display block
        height 25%
        width 80%
        border-right solid 6px black
        border-bottom solid 6px black
        box-sizing border-box
        padding 1em
      &:nth-child(2)
        float left
        height 75%
        width 20%
        border-right solid 3px black
        border-bottom solid 6px black
        box-sizing border-box
      &:nth-child(3)
        float left
        vertical-align top
        width 60%
        height 75%
        border-left solid 3px black
        border-right solid 6px black
        box-sizing border-box
        padding 10%

        input
          border none

  .loginText
    font-size 36px
    font-family Futura

  #loginSubmit
    float left
    width 80%
    height 20%
    display block
    border-left solid 6px black
    border-top solid 6px black

</style>





