<template>
  <ul id="userList">
    <user v-for="user in users" :data="user"></user>
  </ul>
</template>

<script>
  import User from 'components/User'

  export default {
    props: ['users'],
    components: {
      User
    }
  }
</script>

<style lang="stylus">
  #userList
    padding 2em
    list-style-type none
    overflow scroll
    text-transform uppercase

</style>



