<template>
  <div :class="[randomColor, randomWidth]" v-once>
    <article>
      <span class="nom-block">{{data.author.username}}:</span> <br/>
      <span :class="formatData">{{data.body}}</span>
      <div class="fixedBottom text">{{getDate}}</div>
    </article>
  </div>
</template>

<script>
  export default {
    props: ['data'],
    data () {
      return {
        colors: ['blueDiv', 'redDiv', 'yellowDiv', 'whiteDiv', 'whiteDiv', 'whiteDiv'],
        widthClass: ['twentyfive', 'fifty']
      }
    },
    computed: {
      randomColor () {
        var i = Math.floor(Math.random() * 6)
        return this.colors[i]
      },
      formatData () {
        if (this.data.body.match(/!/) && this.data.body.length < 50 || (this.data.body.length <= 20)) {
          return 'title'
        } else {
          return 'text'
        }
      },
      randomWidth () {
        if (this.data.body.length > 400 || (!(this.data.body.match(/^\s+$/)) && this.data.body.length > 100)) {
          return 'hundred'
        } else {
          var i = Math.floor(Math.random() * 2)
          return this.widthClass[i]
        }
      },
      getDate () {
        var date = new Date(this.data.createdAt).toLocaleString()
        return date
      }
    },
    mounted () {
      this.$nextTick(function () {
        var element = document.getElementById('messageList')
        element.scrollTop = element.scrollHeight
      })
    }

  }
</script>

<style lang='stylus' scoped>

  div
    display inline-block
    vertical-align top
    min-width 25%
    max-width 100%

    article
      box-sizing border-box
      width 100%
      height inherit
      padding 20px
      border solid 3px black
      border-left none
      border-right solid 6px black
      position relative

      span
        word-wrap break-word

  .fixedBottom
    position absolute
    bottom 0
    right 0

  .fifty
    width 50%
    height 200px

  .twentyfive
    width 25%
    height 200px

  .hundred
    width 100%
    height auto
    border-right 0

</style>



