<template>
  <div id="messageForm">
    <!-- <div class="text"><span v-if="usertyping.length>0">{{returnUsersTyping}} en train d'écrire</span></div> -->
    <form @submit.prevent="onSubmit">
      <input class="text" type="text" placeholder="...Type something" v-model="message">
      <button type="submit" class="blackDiv">SEND</button>
    </form>
  </div>

</template>

<script>
  export default {
    data () {
      return {
        message: '',
        usertyping: {}
      }
    },
    methods: {
      onSubmit (e) {
        this.sendMessage(this.message)
        this.message = ''
      },
      returnUsersTyping () {
        console.log(this.$store.usertyping.length)
        if (this.$store.usertyping.length > 0) {
          this.usertyping = this.$store.usertyping
          if (this.$store.usertyping.length === 1) {
            return this.$store.usertyping[0].name
          } else if (this.$store.usertyping.length > 3) {
            return [this.$store.usertyping[0].name, this.$store.usertyping[1].name, this.$store.usertyping[2].name].toString() + ' et ' + (this.$store.usertyping.length - 3).toString()
          } else {
            return [this.$store.usertyping[0].name, this.$store.usertyping[1].name, this.$store.usertyping[2].name].toString()
          }
        }
      }
    }
  }
</script>

<style lang="stylus" scoped>

  #messageForm
    box-sizing border-box
    width 100%
    border-top solid 6px black
    height 10%

    >form
      box-sizing border-box
      text-align right
      width inherit
      display flex
      height 100%

      button
        box-sizing border-box
        width 10%

      input
        width 90%

    // >div
    //   box-sizing border-box
    //   padding 0.5em
    //   border-bottom solid 6px black
    //   height 40%
    //   padding 1em

</style>



