<template>
  <div id="chat">
    <message-list :messages="store.messages"></message-list>
    <message-form></message-form>
  </div>
</template>

<script>
  import MessageList from 'components/MessageList'
  import MessageForm from 'components/MessageForm'

  export default{
    props: ['store'],
    components: {
      MessageList,
      MessageForm
    },
    mounted () {
      this.$emit('toggle', true)
    }
  }
</script>

<style lang="stylus">
  #chat
    position relative
    height inherit
    overflow auto
</style>



