<template>
  <div id="messageList">
    <!-- Message List-->
    <message v-for="message in messages" :data="message" :currentWidth="width"></message>
  </div>
</template>

<script>
  import Message from 'components/Message'
  export default {
    props: ['messages'],
    data: () => {
      return {
        lineWidth: 100,
        possibleWidth: [20, 40, 60, 80]
      }
    },
    components: {
      Message
    },
    computed: {
      currentWidth: () => {
        let random
        if (this.lineWidth === 0) {
          this.lineWidth = 100 // it's a new line
        }

        if (this.lineWidth >= 80) {
          random = Math.round(Math.random() * 3)
        } else if (this.lineWidth >= 60) {
          random = Math.round(Math.random() * 2)
        } else if (this.lineWidth >= 40) {
          random = Math.round(Math.random())
        } else if (this.lineWidth >= 20) {
          random = 0
        }

        this.lineWidth -= this.possibleWidth[random]
        return this.possibleWidth[random]
      }
    },
    methods: {

    }
  }
</script>

<style lang="stylus">
  #messageList
    box-sizing border-box
    width 100%
    height 90%
    overflow-y scroll
    overflow-x hidden
</style>



