<template>
  <div id="app">
      <div class="row">
        <div class="appMin" :class="{blueDiv : !isChat}"></div>
        <div class="appMax"></div>
        <div class="appMin" :class="{blueDiv : isChat}"></div>
      </div>
      <div class="row">
        <div class="appMin appMiddle" :class="[{active : isActive}, {blackDiv : isChat}]" @click="onToggle">
          <transition name="fade">
            <svg v-if="!isActive && isChat" id="imgToggle" width="64px" height="64px" viewBox="0 0 64 64" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
                <!-- Generator: Sketch 45.2 (43514) - http://www.bohemiancoding.com/sketch -->
                <title>Icon</title>
                <desc>Created with Sketch.</desc>
                <defs></defs>
                <g id="Page-1" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd" stroke-linecap="square">
                    <g id="Icon" stroke-width="8">
                        <g id="Group" transform="translate(14.000000, 14.000000)">
                            <path d="M-7.10542736e-15,4 L36,4" id="barre1" stroke="#D0021B"></path>
                            <path d="M-7.10542736e-15,20 L27,20" id="barre2" stroke="#4A90E2"></path>
                            <path d="M-7.10542736e-15,36 L18,36" id="barre3" stroke="#F8E71C"></path>
                        </g>
                    </g>
                </g>
            </svg>
           </transition>
          <user-list :users="$store.users" v-if="isActive"></user-list>
        </div>
        <div class="appMax appMiddle">
          <router-view :store="$store" @login="onLogin" @toggle="changeToggable"></router-view>
        </div>
        <div class="appMin appMiddle" :class="{blueDiv : !isChat}"></div>
       </div>
      <div class="row">
        <div class="appMin"></div>
        <div class="appMax"></div>
        <div class="appMin"></div>
      </div>
  </div>
</template>

<script>
import UserList from 'components/UserList'

export default {
  data () {
    return {
      isChat: false,
      isActive: false
    }
  },
  components: {
    UserList
  },
  methods: {
    onLogin (username) {
      this.connect(username)
    },
    changeToggable (binary) {
      if (binary) {
        this.isChat = true
      } else {
        this.isChat = false
      }
    },
    onToggle () {
      if (this.isChat === true) {
        if (this.isActive) {
          this.isActive = false
        } else {
          this.isActive = true
        }
      }
    }
  }
}
</script>

<style lang="stylus">
  @import url('https://fonts.googleapis.com/css?family=Righteous')

  *
    margin 0
    padding 0

  button
    padding 1em
    border-style none
    font-weight bold
    font-size 20px

  input
    box-sizing border-box
    border none
    padding 1em
    &:focus
      outline-width: 0

  .redDiv
    background-color red
    color white

  .yellowDiv
    background-color yellow
    color black

  .blackDiv
    background-color black
    color white

  .blueDiv
    background-color blue
    color white

  .whiteDiv
    background-color white
    color black

  .title
    font-weight bold
    font-family Righteous, cursive
    font-size 36px

  .text
    font-family Futura
    font-size 16px
    lineheight 24px

  .nom-block
    font-family Futura
    font-size 20px
    font-weight bold

  .row
    display flex
    position relative

    >div
      height 10vh
      border 3px solid black

      &.active
        flex-basis 20%
        flex-grow 1

      &.appMiddle
        overflow scroll
        height 78.5vh

      &:first-child
        border-left-width: 0
      &:last-child
        border-right-width: 0

    &:first-child
      .appMin, .appMax
        border-top-width: 0
    &:last-child
      .appMin, .appMax
        border-bottom-width: 0

  .appMin
    flex-basis 5%
    flex-grow 1

  .appMax
    flex-basis 100%
    flex-grow 1

  .fade-enter-active, .fade-leave-active
    transition transform .2s, opacity .3s
    transition-time-function ease-in-out

  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */
    opacity 0
    transform translateX(200px)

  #imgToggle
    position absolute

  #Group:hover

    #barre1, #barre2, #barre3
      opacity 0.9
      transition 0.2s



</style>



